import { EventConstants } from "AC/constants";
import { initAcServices } from "AC/helpers/admin-columns";
initAcServices().addListener(EventConstants.SETTINGS.COLUMN.INIT, function (column) {
    var filterLabel = column.getElement().querySelector('.ac-setting-input_filter_label');
    if (filterLabel) {
        column.getElement().querySelectorAll('.ac-setting-input_label').forEach(function (input) {
            input.addEventListener('keyup', function () {
                filterLabel.placeholder = filterLabel.dataset.defaultTranslation.replace('%s', input.value);
            });
        });
    }
});
